import Vue from "vue";
import vuetify from "vuetify";
import { VUETIFY_CONFIG } from "./Main";
import Admin from "../components/Admin.vue";
function HelloWorld() {
    console.log("Hello World");
    /* HIDING THE LOADING-BAR */
    $(".pre-loader").fadeOut();
    /* DECLARE VUETIFY */
    var vtf = new vuetify(VUETIFY_CONFIG);
    new Vue({
        el: '#app-main',
        data: {},
        components: {
            Admin: Admin
        },
        /* ASSIGN VUETIFY */
        vuetify: vtf
    });
}
$(document).ready(function () {
    HelloWorld();
});
